/* style cho phan thong tin phim */
.movie_detail {
  width: 50%;
}

.movie_detail h2 {
  font-size: 36px;
  padding: 20px 0;
}

.movie_detail .date_vote {
  font-size: 18px;
  padding: 18px 0;
  display: inline-block;
}

.movie_detail p {
  font-size: 16px;
}

/* style cho phan video */
.movie_video {
  width: 45%;

  display: flex;
  justify-content: center;
  align-items: center;
}

.movie_video iframe {
  border: none;
}

.movie_video .movie-detail__image {
  width: 100%;
  height: 400px;
  background-size: contain;
  background-repeat: no-repeat;
}
