/* style for container */
.banner {
  width: 100%;
  height: fit-content;

  position: relative;
}

/* style cho phan hien thi thong tin khi loading, error va no found movie */
.banner-info {
  width: 100%;
  padding: 50px 0;
  text-align: center;
  font-size: 24px;
}

.banner-info h2 {
  font-size: 36px;
}

.banner-info .error {
  color: red;
}

/* style for movie info: image, description */
.banner-image {
  width: 100%;
}

.banner-image img {
  width: 100%;
  object-fit: fill;
}

.banner-detail {
  padding: 0 0 10% 20px;
  height: 100%;
  max-width: 50%;
  color: white;

  position: absolute;
  left: 0%;
  bottom: 0%;
  z-index: 1;

  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
}

/* style nen cho phan description de thong tin hien thi khong bi lan vao mau cua image  */
.banner .banner-detail:after {
  background: linear-gradient(
    90deg,
    rgba(23, 23, 23, 0.8),
    rgba(23, 23, 23, 0)
  );
  bottom: 0;
  content: "";
  left: 0;
  position: absolute;
  right: -200px;
  top: 0;
  z-index: -1;
}

.banner-detail h1 {
  font-size: 50px;
  margin-bottom: 30px;
}

.banner-detail strong {
  display: inline-block;
  padding: 10px 0;
}

.banner-actions {
  display: flex;
  gap: 10px;
}

/* style khi thong tin cua movie khong co duong dan anh */
.no_image {
  height: 500px;
  background-color: gray;
}

/* style khi thay doi kich thuoc screen */
@media (width < 1000px) {
  .banner-detail {
    max-width: 70%;
    padding-bottom: 10%;
  }
}

@media (width < 770px) {
  .banner-detail {
    max-width: 90%;
  }
}
