/* style for loading, error or no found movie */
.movie-list__info {
  width: 100%;
  padding: 50px 0;
  text-align: center;
  font-size: 24px;
}

.movie-list__info .error {
  color: red;
}

.movie-list__info h2 {
  font-size: 36px;
}

/* css for movie list */
.movie-container {
  padding: 12px;

  display: flex;
  gap: 12px;
  flex-wrap: nowrap;
  overflow-x: auto;
}

/* hình nền của thanh cuộn */
.movie-container::-webkit-scrollbar {
  height: 8px;
}

/* style when hover then show scroll but no change height of div */
.movie-container__wrapper {
  position: relative;
  overflow: hidden;
}

.movie-container__wrapper:hover {
  .scroll-overlay {
    display: none;
  }

  /* hình nền của thanh cuộn */
  .movie-container::-webkit-scrollbar {
    background-color: #323131;
  }

  /* phần tử cuộn có thể kéo để thay đổi kích cỡ */
  .movie-container::-webkit-scrollbar-thumb {
    background-color: #a2a1a1;
    width: 30%;
    border-radius: 5px;
  }

  /* không gian trống bên dưới thanh tiến trình */
  .movie-container::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: #323131;
  }
}

/* lop gia de giu cho cho scrollbar, khi scrollbar xuat hien thi lop nay se an di*/
.scroll-overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 8px;
  background-color: rgba(0, 0, 0, 0.5);
  pointer-events: none;
}

/* style movie detail container */
.movie-detail__container {
  width: 100%;
  padding: 20px 12px 20px 30px;

  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
