.button {
  color: white;
  border: 1px solid #484444;
  background-color: #484444;
  border-radius: 2px;
  padding: 10px 25px;
  font-size: 16px;
}

.button:hover {
  cursor: pointer;
  background-color: #8e8a8a;
  border-color: #8e8a8a;
}

.button:focus {
  outline: none;
}

.btn-transparent {
  color: black;
  background-color: transparent;
  border: none;
  padding: 25px;
}

.btn-primary {
  background-color: #00bbec;
  border: none;
  padding: 25px;
}

.btn-primary:hover,
.btn-transparent:hover {
  background-color: #44d3f7;
  color: white;
}

.btn-secondary {
  background-color: rgb(61, 74, 217);
  border: none;
  padding: 25px;
}

.btn-secondary:hover {
  background-color: rgb(69, 100, 193);
}
