.navbar {
  list-style-type: none;
  width: 100%;
  background-color: transparent;
  padding: 10px 20px;

  position: fixed;
  top: 0%;
  left: 0%;
  z-index: 10;

  display: flex;
  justify-content: space-between;
}

.navbar li * {
  cursor: pointer;
}

.navbar h2 {
  color: red;
}

.navbar svg {
  width: 25px;
}

.navbar-bg {
  background-color: black;
}
