.results-container {
  width: 100%;
  padding: 20px 0 30px 0;
}

.results-container h3 {
  font-size: 24px;
  padding-left: 20px;
}

.results {
  width: inherit;
  padding: 20px;

  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: flex-start;
  align-items: flex-start;
}

.movie-detail__container {
  width: 100%;
  padding: 20px 12px 20px 30px;

  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
