.form-container {
  margin-top: 100px;
  padding: 20px;
  width: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
}

.search-form {
  width: 50%;
  max-width: 500px;
  background-color: white;
  border: none;
  border-radius: 5px;
}

.form-control__input {
  width: 100%;
  border-bottom: 2px solid #00bbec;
  padding: 20px;

  display: flex;
  gap: 10px;
  justify-content: center;
  align-items: center;
}

.form-control__input div {
  display: flex;
  justify-content: center;
  align-items: center;
}

.control-input__text {
  width: 90%;
}

.control-input__text input {
  line-height: 30px;
  width: 100%;
  border: none;
  border-bottom: 1px solid transparent;
  font-size: 16px;
}

.control-input__text input:focus {
  outline: none;
  /* border-bottom: 1px solid #00bbec; */
}

.form-control__input svg {
  width: 25px;
  cursor: pointer;
}

.form-control__action {
  width: 100%;
  padding: 40px 20px;

  display: flex;
  gap: 10px;
  justify-content: flex-end;
  align-items: center;
}
