.movie {
  width: fit-content;
}

.movie_item {
  background-size: cover;
  background-repeat: no-repeat;
  transition: transform 300ms ease-in-out;
  cursor: pointer;
}

.movie_item:hover {
  transform: scale(1.05, 1.05);
}

/* style anh ngang, doc */
.backdrop_path {
  height: 200px;
  width: 300px;
}

.poster_path {
  height: 300px;
  width: 200px;
}

/* style khong co anh */
.no_image {
  background-color: rgba(120, 120, 120, 0.2);
  border-radius: 5px;

  display: flex;
  justify-content: center;
  align-items: center;
}
